.WithoutTask{
  width: 180px;
  height: 180px;
  font-size: 3.4rem;
  border-radius: 50%;
  border: none;
  background: var(--principals-buttons);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.WithoutTaskFont{
  font-size: 900;
  text-align: center;
  font-weight: 800;
  color: var(--fondo-page);
}